function changeEndingOfDay(amount: number) {
  const lastNumberOfAmount = amount % 10;
  let word;
  if (lastNumberOfAmount === 1) {
    word = '1 ';
  }
  if (lastNumberOfAmount >= 2 && lastNumberOfAmount <= 4) {
    word = `${amount} `;
  }
  if (lastNumberOfAmount === 0 || lastNumberOfAmount > 4) {
    word = `${amount} `;
  }
  return word;
}

